<template>
  <div class="invite">
    <van-nav-bar title="邀请好友" left-text="" left-arrow @click-left="$router.back()" fixed />
    <invite-poster
      ref="posterRef"
      :nickname="userInfo.nickname"
      :code="invite.inviteCode"
      :avatar="userInfo.avatar"
      :qrcode="qrcodeUrl"
    />
    <invite-list :inviteList="invite.invitees" :add="invite.add" :all="invite.all" />
    <div class="invite__actions__placeholder" />
    <div class="invite__actions">
      <van-button color="#8b6efc" :loading="posterSaveLoading" @click="savePicture()">
        保存到相册
      </van-button>
    </div>
    <a ref="posterSaveRef" style="display: none"></a>
  </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref } from "vue"
import { Button, Divider, Image as VanImage, NavBar, Toast } from "vant"
import InvitePoster from "./InvitePoster.vue"
import InviteList from "./InviteList.vue"
import InviteService, { Invite } from "@/services/invite"
import UserService from "@/services/user"
import qrcode from "qrcode"
import html2canvas from "html2canvas"

export default defineComponent({
  name: "Invite",
  components: {
    InvitePoster,
    InviteList,
    [NavBar.name]: NavBar,
    [Toast.name]: Toast,
    [VanImage.name]: VanImage,
    [Button.name]: Button,
    [Divider.name]: Divider,
  },
  setup() {
    const invite = ref<Invite>({})
    const userInfo = ref({ nickname: "", avatar: "" })
    const posterRef = ref<typeof InvitePoster>()
    const posterSaveRef = ref<HTMLAnchorElement>()
    const posterSaveLoading = ref(false)
    const qrcodeUrl = ref("")

    function getInvites() {
      const loadingToast = Toast({ type: "loading", forbidClick: true, loadingType: "spinner" })
      // 请求邀请列表
      InviteService.getInvites().then(({ status, message, data }) => {
        if (status === 200) {
          invite.value = data
          if (invite.value.inviteCode !== undefined) {
            const inviteSite = `http://h5.cy-cloud.club/register?inviteCode=${invite.value.inviteCode}`
            qrcode.toDataURL(
              inviteSite,
              {
                errorCorrectionLevel: "H",
                type: "image/jpeg",
                quality: 0.3,
                margin: 1,
              },
              (err: string, url: string) => {
                if (err) {
                  Toast(err)
                } else {
                  qrcodeUrl.value = url
                }
              },
            )
          } else {
            Toast("二维码生成失败")
          }
        } else {
          Toast(message)
        }
      })
      // 请求用户数据
      UserService.getCurrentUser()
        .then(({ status, message, data }) => {
          if (status === 200) {
            userInfo.value = data.userInfo
          } else {
            Toast(message)
          }
        })
        .finally(() => {
          loadingToast.clear()
        })
    }

    onMounted(getInvites)

    // 保存图片到相册
    function savePicture(): void {
      if (posterRef.value?.$el) {
        const $poster = posterRef.value?.$el as never as HTMLElement
        const $savePoster = posterSaveRef?.value

        posterSaveLoading.value = true
        html2canvas($poster, {
          scale: 2, //放大一倍，使图像清晰一点
          useCORS: true,
          allowTaint: true,
        })
          .then((canvas) => {
            if ($savePoster) {
              const imageUrl = canvas.toDataURL("image/png")
              $savePoster.href = imageUrl
              $savePoster.download = imageUrl.replace(/(.*\/)*([^.]+.*)/gi, "$2").split("?")[0]
              $savePoster.click()
            }
          })
          .finally(() => {
            posterSaveLoading.value = false
          })
      }
    }

    return { userInfo, posterRef, posterSaveRef, posterSaveLoading, invite, savePicture, qrcodeUrl }
  },
})
</script>

