import client, { ClientResponse } from "@/services/client"

interface Invitee {
  nickname?: string
  mobile?: string
  regdate?: string
}

export interface Invite {
  add?: number
  all?: number
  inviteCode?: string
  invitees?: Invitee[]
}

export default class InviteService {
  public static getInvites(): Promise<ClientResponse<Invite>> {
    return client.get("/users/inviteList.php").then(({ data }) => data)
  }
}
